#mission {
  padding-top: 90px;
  padding-bottom: 90px;
  text-align: center;

  background-image: url(../images/mission/mission-bg.png);
  background-repeat: no-repeat;
  background-position: top center;
  //   background-attachment: fixed;

  background-color: #050315;

  @media (max-width: $mobile) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.mission__wrapper > * + * {
  margin-top: 20px;
}

.mission__category {
  color: $secondaryColor;
  font-size: 30px;
}

.mission__text {
  font-size: 45px;

  max-width: 800px;

  margin-left: auto;
  margin-right: auto;

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }

  @media (max-width: $laptop) {
    font-size: 30px;
  }

  @media (max-width: $mobile) {
    font-size: 20px;
  }
}

