//Media

$desktop-small: 1149px;
$laptop: 929px;
$mobile-big: 820px;
$mobile: 699px;

//ЦВЕТА
$primaryColor: white;
$secondaryColor: #2f82ff;
$thirdColor: #01549f;
$textColor: #000;
$bg-color: #f5f5f7;

//ЗАГОЛОВКИ
.title-h1 {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 90px;
  line-height: 0.98;

  @media (max-width: $desktop-small) {
    font-size: 75px;
  }

  @media (max-width: $laptop) {
    font-size: 65px;
  }

  @media (max-width: $mobile) {
    font-size: 28px;
  }
}

.title-h2 {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 52px;
  line-height: 1.05;

  letter-spacing: -0.016em;

  @media (max-width: $desktop-small) {
    font-size: 45px;
  }

  @media (max-width: $laptop) {
    font-size: 35px;
  }

  @media (max-width: $mobile) {
    font-size: 25px;
  }
}

.title-h3 {
  font-size: 32px;
  font-weight: 600;
  letter-spacing: -0.016em;

  @media (max-width: $laptop) {
    font-size: 28px;
  }

  @media (max-width: $mobile) {
    font-size: 25px;
  }
}

.title-h4 {
  font-weight: 600;
  font-size: 24px;
  line-height: 1.3;

  @media (max-width: $laptop) {
    font-size: 30px;
  }
  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.title-blur {
  font-weight: 600;
  font-size: 32px;
  line-height: 1.3;

  letter-spacing: -0.016em;

  color: #9ccaff;

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.title-thin {
  font-family: normalFont;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.3;

  letter-spacing: -0.016em;

  color: rgba(0, 0, 0, 0.9);
}

//КНОПКИ
.button {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 25px;
  padding-left: 25px;

  background-color: $secondaryColor;
  border-radius: 9000px;

  text-align: center;
  cursor: pointer;
  color: $primaryColor;

  font-weight: 500;
  font-size: 24px;

  @media (max-width: $mobile) {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 16px;
  }
}

.button--secondary {
  display: block;
  background-color: #dedee7;
  border-radius: 9000px;

  text-align: center;
  cursor: pointer;
  color: $thirdColor;
}

/* Reset and base styles  */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%; /* sticky footer */
  scroll-behavior: smooth;
}

body {
  height: 100%; /* sticky footer */
  display: flex; /* sticky footer */
  flex-direction: column; /* sticky footer */
  margin: 0 !important;
  font-family: headerFont, sans-serif !important;
  font-size: 64px;
  font-weight: 700;

  color: white;
  background-color: $bg-color !important;
}

body p {
  line-height: 1.3em;
}

body.noscroll {
  overflow: hidden;
}

body > * {
  flex-shrink: 0; /* sticky footer */
}

a {
  text-decoration: none;
}

#overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 101;
}

#overlay.active {
  display: block;
}

.error {
  color: red;
}

input,
textarea,
button {
  font-family: inherit;
  padding: 0;
}

button {
  border: none;
  outline: none;
}

label {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

legend {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
}

/* Grid styles */

.container {
  max-width: 1680px;
  margin: 0 auto;
  padding-left: 90px;
  padding-right: 90px;

  @media (max-width: $laptop) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

/* Utility */

.none {
  display: none !important;
}

/* Sticky Footer */

.footer {
  margin-top: auto; /* sticky footer */
}
