#jobs {
  padding-top: 90px;
  padding-bottom: 90px;

  @media (max-width: $mobile) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.jobs__header {
  margin-bottom: 50px;
  max-width: 860px;
  color: $secondaryColor;
}

.jobs__row {
  display: flex;
  justify-content: space-between;

  @media (max-width: $laptop) {
    flex-direction: column;
  }
}

.jobs__row > * + * {
  @media (max-width: $laptop) {
    margin-top: 30px;
  }
}

.job-desc {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.35;

  color: $textColor;

  max-width: 475px;

  @media (max-width: $desktop-small) {
    font-size: 30px;
  }
  @media (max-width: $mobile) {
    font-size: 20px;
  }
}

.job-name {
  color: $thirdColor;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.04;

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }
  @media (max-width: $mobile) {
    font-size: 25px;
  }
}

.job-item {
  padding: 50px;

  background: $primaryColor;
  border-radius: 48px;

  width: 46%;

  @media (max-width: $laptop) {
    width: 100%;
  }
  @media (max-width: $mobile) {
    padding: 35px;
  }
}

.job-item > * + * {
  margin-top: 30px;
}

.jobs__list > * + * {
  margin-top: 60px;

  @media (max-width: $laptop) {
    margin-top: 30px;
  }
}

.job-column {
  width: 46%;
  & .job-item {
    width: 100%;

    & .job-desc {
      margin-top: 0;
    }
  }

  @media (max-width: $laptop) {
    width: 100%;
  }
}

.job-column > * + * {
  margin-top: 60px;

  @media (max-width: $laptop) {
    margin-top: 30px;
  }
}
