#clients {
  padding-top: 90px;
  padding-bottom: 90px;

  font-size: 40px;
  font-weight: 400;
  font-family: normalFont, sans-serif;
  color: $textColor;

  @media (max-width: $mobile) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.clients__header {
  max-width: 915px;

  & .title-h2 {
    color: $secondaryColor;
  }

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }

  @media (max-width: $laptop) {
    font-size: 30px;
  }

  @media (max-width: $laptop) {
    font-size: 20px;
  }
}

.clients__header > * + * {
  margin-top: 40px;

  @media (max-width: $mobile) {
    margin-top: 30px;
  }
}

.clients__wrapper > * + * {
  margin-top: 180px;

  @media (max-width: $desktop-small) {
    margin-top: 120px;
  }
  @media (max-width: $mobile) {
    margin-top: 90px;
  }
}

.clients__top > * + *,
.clients__bottom > * + * {
  margin-top: 60px;
}

.clients__row {
  display: flex;
  justify-content: space-between;
}

.client-item {
  display: flex;
  align-items: center;
}

.client-item > * + * {
  margin-left: 30px;

  @media (max-width: $laptop) {
    margin-left: 20px;
  }
}

.clients-left > * + *,
.clients-right > * + * {
  margin-top: 60px;
}

.client-name {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 45px;

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }

  @media (max-width: $laptop) {
    font-size: 25px;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
  }
}

.client-img{
  @media (max-width: $desktop-small) {
    width: 80px;
  }

  @media (max-width: $laptop) {
    width: 60px;
  }
  @media (max-width: $mobile) {
    width: 45px;
  }
}

.clients__more {
    margin-top: 90px;
  font-family: normalFont, sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 1;

  text-align: center;

  color: #b1b1c2;

  @media (max-width: $desktop-small) {
    font-size: 35px;
    margin-top: 50px;
  }

  @media (max-width: $laptop) {
    font-size: 25px;
    margin-top: 50px;
  }
  @media (max-width: $laptop) {
    font-size: 18px;
    margin-top: 40px;
  }
}
