#contacts {
  padding-bottom: 60px;

  background-color: $thirdColor;
}

.contacts__header {
  padding-top: 60px;
  padding-bottom: 30px;
  color: $primaryColor;

  margin-bottom: 50px;

  @media (max-width: $mobile) {
    margin-bottom: 30px;
  }
}

.contacts__item {
  display: flex;
  align-items: center;

  & a {
    color: $primaryColor;
    margin-bottom: 5px;
    font-size: 40px;
    font-family: normalFont, sans-serif;
    font-weight: 400;

    @media (max-width: $desktop-small) {
      font-size: 35px;
    }
    @media (max-width: $laptop) {
      font-size: 30px;
    }
  }

  & i {
    color: #80c1ff;
    font-size: 40px;
    @media (max-width: $desktop-small) {
      font-size: 35px;
    }
    @media (max-width: $laptop) {
      font-size: 30px;
    }
    @media (max-width: $mobile) {
      font-size: 25px;
    }
  }
}

.contacts__adress {
  color: $primaryColor;
  font-size: 40px;
  font-family: normalFont, sans-serif;
  font-weight: 400;

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }
  @media (max-width: $laptop) {
    font-size: 30px;
  }
  @media (max-width: $mobile) {
    font-size: 25px;
  }
}

.contacts__item > * + * {
  margin-left: 20px;
}

.contacts__item-adress {
  align-items: flex-start;

  & i {
    margin-top: 10px;
  }
}

.contacts__info > * + * {
  margin-top: 50px;
}

.contacts__row {
  display: flex;

  justify-content: space-between;

  @media (max-width: $mobile) {
    flex-direction: column;

    & > * + * {
      margin-top: 60px;
    }
  }
}

.contacts__logo {
  @media (max-width: $desktop-small) {
    max-width: 250px;
  }
  @media (max-width: $desktop-small) {
    max-width: 200px;
  }
}
