#work {
  padding-top: 90px;
  padding-bottom: 90px;

  color: #000000e5;

  @media (max-width: $mobile) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.work__wrapper {
  text-align: center;
}

.work__wrapper > * + * {
  margin-top: 30px;
}

.work__about {
  color: $secondaryColor;
  font-size: 35px;
}

.work__text {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;

  font-size: 45px;

  @media (max-width: $desktop-small) {
    font-size: 35px;
  }

  @media (max-width: $laptop) {
    font-size: 30px;
  }

  @media (max-width: $mobile) {
    font-size: 20px;
  }
}
