#header {
  padding-top: 60px;
  padding-bottom: 120px;

  background-color: #003272;

  background-image: url(../images/header/header-bg.png);
  background-repeat: no-repeat;
  background-position: top center;

  @media (max-width: $mobile) {
    padding-top: 90px;
    padding-bottom: 90px;
  }
}

.header__nav {
  & img {
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;

    @media (max-width: $mobile) {
      max-width: 250px;
    }
  }
}

.header__text {
  margin: 0 auto;
  margin-top: 130px;
  text-align: center;

  max-width: 700px;

  @media (max-width: $desktop-small) {
    max-width: 530px;
    margin-top: 110px;
  }
  @media (max-width: $mobile) {
    max-width: 530px;
    margin-top: 90px;

    & h1 {
      font-size: 35px;
    }
  }
}
