#monetization {
  padding-bottom: 90px;
}

.monetization__pattern > * + * {
  margin-top: 50px;

  @media (max-width: $mobile) {
    margin-top: 30px;
  }
}

.monetization-item {
  padding: 40px;
  background: #ffffff;
  border-radius: 48px;

  color: $textColor;

  width: 48%;

  & h4 {
    font-weight: 700;
    font-size: 48px;
    line-height: 1.04;
    min-height: 72px;

    @media (max-width: $desktop-small) {
      font-size: 35px;
    }

    @media (max-width: $mobile) {
      min-height: unset;
      font-size: 25px;
    }
  }

  & p {
    font-weight: 700;
    font-size: 36px;
    line-height: 1.35;

    max-width: 500px;

    @media (max-width: $desktop-small) {
      font-size: 30px;
    }

    @media (max-width: $desktop-small) {
      font-size: 20px;
    }
    
  }

  
}

.monetization-item > * + * {
  margin-top: 25px;
}

.monetization__header {
  color: $secondaryColor;
  margin-bottom: 50px;
}

.monetization__top {
  display: flex;
  justify-content: space-between;

  @media (max-width: $mobile) {
    flex-direction: column;

    & .monetization-item{
      width: 100%;
    }
  }
}

.monetization__top > * + * {
  @media (max-width: $mobile) {
    margin-top: 30px;
  }
}

.monetization__bottom {
  & .monetization-item {
    width: 100%;

    & p {
      max-width: 960px;
    }
  }
}
