#opportunity {
  padding-top: 100px;
  padding-bottom: 100px;
  background-image: url(../images/opportunity/opportunity-bg.png);
  background-repeat: no-repeat;
  background-position: top center;
  background-color: #020f32;

  color: $primaryColor;

  & span {
    color: $secondaryColor;
  }

  @media (max-width: $mobile) {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.opportunity__wrapper > * + * {
  margin-top: 70px;
}

.opportunity__text,
.opportunity__info {
  font-family: normalFont, sans-serif;
  font-weight: 400;
  font-size: 40px;
  line-height: 1.35;
}

.opportunity__text{
  @media (max-width: $desktop-small) {
    font-size: 35px;
  }

  @media (max-width: $desktop-small) {
    font-size: 30px;
  }
  @media (max-width: $mobile) {
    font-size: 20px;
  }
}

.opportunity__info {
  font-size: 36px;

  @media (max-width: $desktop-small) {
    font-size: 30px;
  }

  @media (max-width: $desktop-small) {
    font-size: 25px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.opportunity__text > * + * {
  margin-top: 50px;
}
